.centerStyle{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap:5px;
  padding: 10px;
}

.centerLabel{
  color: #fff;
  font-size: 18px;
  text-align: center;
  font-weight: 700;
}

.cover {
    align-items: flex-start;
    background-color: rgba(0, 0, 0, .8);
    border-radius: 8px;
    display: flex;
    height: 100%;
    justify-content: center;
    position: absolute;
    width: 100%;
    z-index: 5;
    padding-top: 25px;
    padding-left: 5px;
    padding-right: 5px;
}

.modal{
    align-items: center;
    background-image: linear-gradient(to top left, #ffb800, #9225ff);
    border: none;
    border-radius: 30px;
    color: #fff;
    display: flex;
    flex-direction: column;
    gap: 5px;
    padding: 20px;
}

.modalLabel{
    color: #fff;
    font-family: DM Sans;
    font-weight: 700;
    margin: 10px;
    text-align: center;
}

.formContainer{
    background-color: #280a46;
    border: 3px solid #ffb800;
    border-radius: 30px;
    box-shadow: inset 0 0 10px 5px #9225ff;
    display: flex;
    flex-direction: column;
    gap: 10px;
    justify-content: center;
    padding: 10px;
}

.form{
    color: #fff;
    font-family: DM Sans;
    font-weight: 700;
    padding: 5px;
    text-align: center;
}

.label{
    display: flex;
    flex-direction: column;
    margin-bottom: 16px;
    text-align: start
}

.input{ 
background-color: #9225ff;
    border: 1px solid #fff;
    border-radius: 30px;
    box-shadow: inset 0 0 15px 10px rgba(0, 0, 0, .5);
    color: #fff;
    display: block;
    font-family: Work Sans, sans-serif;
    font-weight: 700;
    margin-top: 3px;
    padding: 10px;
    width: 100%;
}

.modalButton{
background-color: #ffa5ba;
    background-color: #9225ff;
    border: none;
    border-radius: 30px;
    color: #fff;
    cursor: pointer;
    filter: drop-shadow(0 8px 8px rgba(0, 0, 0, .3));
    font-family: Work Sans, sans-serif;
    font-weight: 700;
    padding: 8px;
    text-align: center;
}

.modalButton:hover{
 background-image: linear-gradient(to top left, #ffb800, #9225ff);
}

.childWrapper{
    padding:15px;
    position: relative;
    display:flex;
    align-items: center;
    justify-content: center;
}